/**
 * Constructeur d'apps.
 */
export interface IAppBuilder<ENVIN, ENVOUT> extends Area<IAppCtx<ENVIN>, IApp<ENVOUT>> {

	/**
	 * Cet appBuilder peut-il fournir une app avec cette dÃ©finition (gÃ©nÃ©ralement json issu du hash d'une url).
	 */
	isAppDefMatch(data: JAppDef): boolean

	/**
	 * retourne les params au juste nÃ©cessaire
	 * Remarque : modifie l'objet passÃ© en paramÃ¨tre
	 */
	normalizeAppDef?(data: JAppDef): void

	/**
	 * Construit une app.
	 * Note : une IApp ne doit pas dÃ©pendre de son contexte DOM parent (repositionnement libre)
	 */
	buildBody(ctx: IAppCtx<ENVIN>, lastDatas?: JLastDatas): IApp<ENVOUT>

	loadBody(ctx: IAppCtx<ENVIN>, lastDatas?: JLastDatas): Promise<IApp<ENVOUT>>

}

/** Contexte pour la construction d'une App. */
export interface IAppCtx<ENVIN> extends IRegPointer<ENVIN> {

	/** DonnÃ©es issues du hash de l'URL ou d'une autre source pour crÃ©er cette app. */
	appDef: JAppDef

	/** DonnÃ©e de restauration de la app selon son dernier Ã©tat enregistrÃ©. */
	lastDatas?: JLastDatas

	/**
	 * Informe l'app Ã  sa construction qu'elle dispose d'une barre de titre riche
	 * qu'elle pourra exploiter lorsque le contexte appellera IApp.getHeadTitleBar()
	 * (juste aprÃ¨s son initialisation). Au cours de la vie d'une mÃªme app IApp.getHeadTitleBar()
	 * pourra Ãªtre appelÃ© plusieurs fois et peut retourner toujours le mÃªme Ã©lÃ©ment.
	 * La app peut garder une ref Ã  cet Ã©lÃ©ment pour des mises Ã  jour dynamiques.
	 */
	hasHeadTitleBar?: boolean
}

/**
 * PropriÃ©tÃ©s json de dÃ©finition d'une app. Ces donnÃ©es permettent
 * - d'identifier la app Ã  construire. Exemple app=wsp, app=dynGen, app=editDepot, app=userAdmin...
 * - de fournir les donnÃ©es minimale pour reconstruire une vue Ã©quivalente dans une nouvelle
 *   fenetre. Exemple pour une app d'affichage
 *     - d'un atelier chain : wspRef.
 *     - d'un dynGen sur un item : wspRef + genCode + skin
 *
 * Ces propriÃ©tÃ©s ne contiennent pas l'URL du serveur (univers) ni du user courant qui relÃ¨vent de l'appFrame.
 */
export type JAppDef = {
	/** app fallback appelÃ©e en cas d'impossibilitÃ© d'afficher cette app
	 * Note : exploitÃ© uniquement lors du traitement de l'URL du hash
	 */
	fbApp?: JAppDef
}

/**
 * ReprÃ©sente une App instanciÃ©e sous forme d'un Custom Element DOM.
 */
export interface IApp<ENV> extends IView, ILastDatasBuilder, IRegPointer<ENV & IUiEnv>, IEltInitableAsync, IAccelKeyMgrPointer<IApp<ENV>> {

	/** Raison de l'Ã©chec de l'app
	 * Note : renseignÃ© SSi l'app est chargÃ©e */
	appErrorStatus?: 'accessDenied' | string

	/** Si cette app peut-etre affichÃ©e sans user connectÃ©. */
	anonymousAllowed?: boolean

	/**
	 * DÃ©finition de l'Ã©tat courant de la app. Peut-Ãªtre utilisÃ© pour construire le hash d'une url
	 * et rÃ©obtenir cette app avec les mÃªmes principales donnÃ©es chargÃ©es.
	 */
	readonly appDef: JAppDef

	/**
	 * Met Ã  jour la dÃ©finition de cette app du fait d'un event extÃ©rieur, par exemple dÃ» Ã  une
	 * modification de hash de l'URL.
	 *
	 * Inversement, si la app met Ã  jour sa propre dÃ©finition, un DOM event "c-appdef-change" (bubbles=true)
	 * doit Ãªtre gÃ©nÃ©rÃ© pour notifier l'appFrame. Si la app ne peut revenir dans l'Ã©tat prÃ©cÃ©dent, une
	 * propriÃ©tÃ© 'revertable' doit Ãªtre mise Ã  false dans les CustomEvent.detail : {bubbles: true, detail: {revertable:false}}
	 *
	 * @return true si cette app est toujours valide avec cette nouvelle dÃ©finition, false si l'appFrame
	 * appelant devrait repasser par ses IAppBuilder pour remplacement de l'app.
	 */
	updateAppDef(def: JAppDef): boolean


	/**
	 * Offre la possibilitÃ© Ã  l'app d'injecter une structure html plus riche que son simple titre IApp.getLabel().
	 * Exemple : ajout d'un menu d'options, etc.
	 * Ce contenu fournit par la App doit se conformer Ã  un espace de type barre de titre.
	 * Cette mÃ©thode sera appelÃ©e par le contexte applicatif (si il dispose de cette possbilitÃ©) ; Ã  l'init
	 * de la app, elle saura qu'elle aura cette possibilitÃ© via la propriÃ©tÃ© IAppCtx.hasHeadTitleBar=true.
	 */
	getHeadTitleBar?(): HTMLElement

	/**
	 * Offre la possibilitÃ© Ã  l'app de capter des events claviers globaux au niveau de la window,
	 * (mÃªme si la app ne dÃ©tient pas le focus) si le container le permet
	 */
	accelKeyMgr?: AccelKeyMgr<IApp<ENV>>

	// /** EntÃªte standard d'une app, optionnel */
	// readonly appHeader?: AppHeader<IApp<ENV>>
}

export function isApp<ENV>(tag: HTMLElement): tag is IApp<ENV> {return tag && ('appDef' in tag)}

/**
 * Feature du Desk pour constituer un cadre graphique pour UNE SEULE app simultannÃ©e.
 * Se constitue de :
 * - 1 header (toolbar : appframe:header:toolbar)
 * - 1 zone main Ã  populer
 * - 1 footer : zone info, user
 *
 * GÃ¨re l'authentification initiale de la page fondÃ© sur REG.reg.env.universe.
 * Un menu "Options" ajoutÃ© par dÃ©faut en fin de toolbar du header (masquÃ© automatiqument si vide).
 *
 * TODO dÃ©river un AppFrameMultiDeskFeat pour un concept de bureau avec plusieurs apps lancÃ©es simultannÃ©ment et une barre en bas...
 *
 */
export class AppFrameDeskFeat extends Desk {

	static add(desk: Desk, options?: OAppFrameDeskFeatOptions & OAppFrameOptions): AppFrameDeskFeat {
		if (this.isIn(desk)) return desk;
		LastDatasDeskFeat.add(desk);
		ContextMenuDeskFeat.add(desk);
		desk.assignProps(this);
		const d = desk as AppFrameDeskFeat & LastDatasDeskFeat;
		d._appBuilders = [];
		d.initialTitle = document.title;

		//Construction d'un id de session unique dans le navigateur.
		d.ldSession = location.search.substr(1);
		const bcSessions = new BroadcastChannel(location.pathname);
		const rand = Math.random();
		bcSessions.onmessage = (ev: MessageEvent) => {
			if (ev.data.req === "getSessions") {
				//un autre onglet demande les sessions en cours, on lui rÃ©pond.
				bcSessions.postMessage({resp: 'getSessions', session: d.ldSession, r: ev.data.r});
			} else if (ev.data.resp === "getSessions" && ev.data.r === rand) {
				//rÃ©ponse Ã  notre requÃªte.
				if (ev.data.session === d.ldSession) d.onLdSessionConflict();
			}
		};
		bcSessions.postMessage({req: 'getSessions', r: rand});

		//Bloque tout drop natif dans la page.
		document.ondragenter = document.ondragover = document.ondrop = (ev: DragEvent) => {
			ev.dataTransfer.dropEffect = 'none';
			ev.preventDefault();
		};

		//Le plus tÃ´t possible : chargement du user authentifiÃ© courant
		REG.reg.addToList(Desk.LC_init, 'loadUser', 1, () => {
			d._auth = (REG.reg.env as IBasicUniversePointer).universe.auth;
			return d._auth.fetchUser();
		}, -1000);

		//En fin de chargement : construction de l'ihm root (remplaÃ§ant l'ihm d'attente)
		REG.reg.addToList(Desk.LC_load, 'buildUi', 1, () => {
			const body = document.body as HTMLElement & ILastDatasBuilder & ILastDatasHolder;

			//LastDatas
			body.buildLastDatas = (p: JLDAppFrame) => {
				if (d._app) {
					p.appDef = d._app.appDef;
					p.appLD = LASTDATAS.buildLastDatas({}, d._app, true);
				}
			};
			body.getParentLastDatas = (forChild: HTMLElement): JLastDatas | null => {
				const ld = (desk as LastDatasDeskFeat).lastDatas as JLDAppFrame;
				return ld ? ld.appLD : null;
			};

			//Construction du body
			body.textContent = null;
			REG.reg.installSkin('appFrame');

			const actions = <Actionables id="headerActions" actions="appframe:header:toolbar" group-order="*" ui-context="dialog"/>;
			const portalTi = <span id="portalTi">{document.title}</span>;
			const collapseActions = <ButtonToggle id="collapseActions" title="Masquer" icon="/@skin@/core/widgets/appFrame/collapse.svg" onclick={function (this: ButtonToggle) {
				this.toggleOn = portalTi.hidden = actions.hidden = !this.toggleOn;
				this.title = this.toggleOn ? "Afficher" : "Masquer";
			}}/>;
			d.appBar = <span id="appBar"/>;
			d.mainBar = <div id="mainBar">
				<ShadowJsx skin="appFrame/mainBar" skinOver="webzone:invert">
					<Button id="home" onclick={() => {(desk as AppFrameDeskFeat).gotoHome()}}>
						<div id="logo"><img src={options && options.iconUrl || "/@skin@/core/logoSC.svg"} title={options.appName}/></div>
						{portalTi}
					</Button>
					{actions}
					{collapseActions}
				</ShadowJsx>
			</div>;
			d.header = body.appendChild(
				REG.reg.getSvc('appFrameHeader') as HTMLElement ||
		  <header>
						{d.mainBar}
						{d.appBar}
			  <div id="loggedUserBox"><LoggedUser Ã®={{icon: "--user-icon", reg: (options && options.authReg) || REG.reg}}/></div>
		  </header>
			) as HTMLElement;

			//body.appendChild(<hr/>);

			d.mainCtn = body.appendChild(REG.reg.getSvc('appFrameMain') as Element || <main/>) as HTMLElement;
			d.mainCtn.addEventListener("c-view-change", function (this: HTMLElement, ev: CustomEvent) {
				if ((ev.target as Element).parentElement === this) (desk as AppFrameDeskFeat).refreshTitle();
			})
			d.mainCtn.addEventListener("c-appdef-change", function (this: HTMLElement, ev: CustomEvent) {
				if ((ev.target as Element).parentElement === this) (desk as AppFrameDeskFeat).updateUrlHash(ev.detail && ev.detail.revertable === false);
			});

			//Masquage du header en mode fullscreen
			if (document.fullscreenEnabled) {
				d.header.append(<Button id="fullscreen" Ã®={{reg: REG.reg, icon: "/@skin@/core/actions/portalbar/fullscreen.svg", title: "Contenu en plein Ã©cran"} as OButtonInit} onclick={AppFrameDeskFeat.requestFullscreen}/>);
			}

			if (UrlHashObjDeskFeat.isIn(d)) d.onUrlHashChange.add(() => d.setMainAppFromHash());

			//On tue le racourcis ctrl+S du navigateur qui n'a pas de sens dans un appFrame.
			REG.reg.addToList(Desk.LIST_accelKeys, "s-accel", 1, new Action<any>('killCtrlS').setExecute(function (c, ev) {ev.preventDefault()}));
			REG.reg.addToList(Desk.LIST_accelKeys, "F1", 1, new Action<any>('killF1').setExecute(function (c, ev) {ev.preventDefault()}));

			d._auth.listeners.on('loggedUserChanged', async (oldUser: JUser, newUser: JUser) => {
				//on clos l'app actuelle
				await d.closeMainApp(true);

				if (oldUser && !newUser) {
					//DÃ©connexion, on efface l'Ã©tat courant dans le hash
					if (UrlHashObjDeskFeat.isIn(d)) await d.setUrlHash(null);
				}

				if (newUser) {
					//on recharge Ã  partir du hash et des (nouveaux) lastDatas
					await d.setMainAppFromHash((desk as LastDatasDeskFeat).lastDatas);
				} else {
					// Plus de user car anonymous interdit.
					// on force l'affichage du login.
					const loginAct = REG.reg.getSvc('userLogin') as Action<Element>;
					loginAct.execute(body);
				}
				//On refresh les bars.

				BASIS.refreshTree(d.mainBar.shadowRoot);
				BASIS.refreshTree(d.header);
				//if (d.footer) BASIS.refreshTree(d.footer);
			}, 100); //La reconstruction de la page devrait intervenir en dernier, aprÃ¨s les rechargements, lastDaats, userDatas...

			/** On controle qu'on peut clore l'app courante avant logOut. */
			d._auth.listeners.on('beforeLogout', (user: JUser): void | 'stop' | Promise<void | 'stop'> => {
				if (d._app) return VIEWS.canHideView(d._app).then((canClose: boolean) => (canClose ? undefined : 'stop') as void | 'stop');
			});

			if (!d._auth.config.anonymousUser) {
				//Si anoymous pas autorisÃ©, affichage de l'Ã©cran de log au chargement de la page.
				if (d._auth.currentUser == null) {
					const loginAct = REG.reg.getSvc('userLogin') as Action<Element>;
					loginAct.execute(body);
				}
			}

			//Lancement de la app si forcÃ©
			if (d._app) d.setMainApp(d._app);
			//Sinon on tente de la lancer Ã  partir du hash et des lastDatas.
			else d.setMainAppFromHash((desk as LastDatasDeskFeat).lastDatas);
		}, 1000);

		//Gestion des erreurs de connexion rÃ©seau
		REG.reg.addToList(Desk.LC_load, 'netError', 1, () => {
			const universe = (REG.reg.env as IBasicUniversePointer).universe;
			const url = new URL(universe.httpFrames.web.config.execFrameUrl.url);
			if (url.hostname !== 'localhost' && url.hostname !== '127.0.0.1') {
				d.isServerLocal = false;
				//Par l'Ã©coute de l'evt "offline" on dÃ©tecte une coupure rÃ©seau.
				window.addEventListener('offline', refreshOffline);
				window.addEventListener('online', refreshOffline);
				refreshOffline();
			} else d.isServerLocal = true;
		}, 1010);
		return d;
	}

	static isIn(desk: Desk): desk is AppFrameDeskFeat {return 'setMainAppFromHash' in desk}

	protected onGloablKeyDown(this: void, ev: KeyboardEvent) {
		if (window.desk.accelKeys.handleKeyboardEvent(ev, window) === 0) {
			(window.desk as AppFrameDeskFeat)._app?.accelKeyMgr?.handleKeyboardEvent(ev, (window.desk as AppFrameDeskFeat)._app);
		}
	}

	/**
	 * Ajout d'un appBuilder dans cet appFrame
	 */
	addAppBuilder(appBuilder: IAppBuilder<any, any>) {
		this._appBuilders.push(appBuilder);
	}

	/**
	 * App construite par dÃ©faut, si aucune autre app disponible.
	 * InitialisÃ© avec le REG.reg racine et l'appDef issu du hash
	 * pour indiquer une Ã©ventuelle erreur dans le hash.
	 */
	setDefaultAppBuilder(appBuilder: IAppBuilder<any, any>) {
		this._defaultAppBuilder = appBuilder;
	}

	/**
	 * Gestionnaire de conflit d'id de session.
	 * La version actuelle gÃ©nÃ¨re un id alÃ©atoire, un prompt au user pourrait aussi Ãªtre envisagÃ©.
	 */
	onLdSessionConflict() {
		const s = (desk as LastDatasDeskFeat).ldSession = (Math.round(Math.random() * 9999999)).toString(36);
		//On ajoute un ~ pour repÃ©rer que c'est une session random, donc un code non signifiant
		//pour le user, Ã  remplacer par un nÂ° dans l'ihm de gestion des sessions.
		location.search = "?~" + s;
	}

	/** Surveillance d'une connexion webSocket d'un univers pour notifier une iterruption d'accÃ¨s au serveur. */
	surveyWsConnexion(ws: IWsExecFrame) {
		//Par l'Ã©coute de la connection WS, on dÃ©tecte l'arret du serveur.
		ws.lcListeners.on("closed", function (code: number) {
			if (code !== 1000) (desk as AppFrameDeskFeat).setServerOff(true);
			else {
				//cloture sur user inactif.
				//plus de ws pour dÃ©tecter un arrÃªt serveur, on Ã©coute les erreurs de connection ?
				//XXX Ã©couter les erreurs fetch ? window.addEventListener('error', onWindowError);
			}
		});
		ws.lcListeners.on("opened", function () {
			//XXX Ã©couter les erreurs fetch ? window.removeEventListener('error', onWindowError);
			(desk as AppFrameDeskFeat).setServerOff(false);
		});
		ws.msgListeners.on("liaise", this.onLiaiseMsg);
	}

	onLiaiseMsg(this: void, m: JMaintenanceMsg) {
		//console.log("liaise:::::::::::", m);
		if (m.type !== "maintenance") return;
		if (!m.msg && m.opType) {
			//COMPAT XUL
			switch (m.opType) {
			case "update" :
				m.msg = "ATTENTION, une mise Ã  jour du serveur est imminente";
				break;
			case "restart":
				m.msg = "ATTENTION, un redÃ©marrage du serveur est imminent";
				break;
			case "shutdown":
				m.msg = "ATTENTION, l'arrÃªt du serveur est imminent";
				break;
			}
		}
		(desk as AppFrameDeskFeat).setMaintenanceMsg(m.msg, m.until);
	}

	/** DÃ©tection de l'Ã©tat du serveur. */
	isServerOff: boolean;

	/** Le serveur est-il sur le net ou local (localhost). */
	isServerLocal: boolean;

	setServerOff(off: boolean) {
		this.isServerOff = off;
		this.maintenanceMsg = null; //pour ne pas afficher un msg si relancÃ© plus tot que le until.
		this.refreshErr();
	}

	maintenanceMsg?: string;
	maintenanceMsgUntil?: number;

	/** Affiche un message de maintenance. */
	setMaintenanceMsg(msg: string, until?: number) {
		const now = Date.now();
		if (!until) until = now + 30000; //msg affichÃ© 30s par dÃ©faut.
		const delay = until - now;
		if (delay > 500) {
			this.maintenanceMsgUntil = until;
			this.maintenanceMsg = msg;
			this.refreshErr();
			setTimeout(() => {
				if (this.maintenanceMsg === msg) {
					//msg pas modifiÃ© entre temps, on reset
					this.maintenanceMsg = null;
					this.refreshErr();
				}
			}, delay);
		}
	}

	refreshErr() {
		if (this.isServerOff) {
			this.setHeadband("Votre connexion au serveur est interrompue ou instable", "error");
		} else if (!this.isServerLocal && !navigator.onLine) {
			this.setHeadband("Vous n'Ãªtes pas connectÃ©Â·e au rÃ©seau, accÃ¨s impossible au serveur.", "error");
		} else if (this.maintenanceMsg) {
			this.setHeadband(this.maintenanceMsg, "warning");
		} else {
			this.setHeadband(null);
		}
	}

	setHeadband(msg: string, level?: 'info' | 'error' | 'warning', icon?: string) {
		if (msg) {
			if (!this._headband) this._headband = <MsgLabel class="headband"/> as MsgLabel;
			if (!this._headband.isConnected) document.body.insertBefore(this._headband, this.mainCtn);
			this._headband.setCustomMsg(msg, level, icon);
		} else {
			if (this._headband && this._headband.isConnected) this._headband.remove();
		}
	}

	protected _headband: MsgLabel;

	/** */
	async setMainAppDef(appDef: JAppDef): Promise<boolean> {
		for (const bd of this._appBuilders) {
			if (bd.isAppDefMatch(appDef)) {
				return this.setMainApp(await bd.loadBody({reg: REG.reg, appDef, hasHeadTitleBar: true}));
			}
		}
		return false;
	}

	async setMainAppFromHash(useLD?: JLDAppFrame): Promise<void> {
		if (this._updtHashFromHere) return; //bloque rÃ©entrance quand la modif vient de nous.
		const appDef = UrlHashObjDeskFeat.isIn(this) ? this.getUrlHash() : useLD && useLD.appDef;
		try {
			if (appDef) {
				//L'app actuelle peut-elle prendre en charge cet appDef.
				if (this._app && this._app.updateAppDef(appDef)) return;
				//On cherche un appBuilder compatible
				for (const bd of this._appBuilders) {
					if (bd.isAppDefMatch(appDef)) {
						if (!await this.setMainApp(await bd.loadBody({
							reg: REG.reg,
							appDef,
							lastDatas: useLD && useLD.appDef && bd.isAppDefMatch(useLD.appDef) ? useLD.appLD : null,
							hasHeadTitleBar: true
						}))) {
							let fbApp = appDef.fbApp;
							while (fbApp) {
								if (await this.setMainAppDef(fbApp))
									return;
								fbApp = fbApp.fbApp;
							}
							throw "";
						}
						return;
					}
				}
				if (useLD && useLD.appDef) {
					//Pas trouvÃ©, on recherche la app des lastDatas
					for (const bd of this._appBuilders) {
						if (bd.isAppDefMatch(useLD.appDef)) {
							if (!await this.setMainApp(await bd.loadBody({
								reg: REG.reg,
								appDef: useLD.appDef,
								lastDatas: useLD.appLD,
								hasHeadTitleBar: true
							}))) throw "";
							return;
						}
					}
				}
			}
			//Rien trouvÃ©, on tente la defaultAppBuilder.
			if (this._defaultAppBuilder) {
				if (!await this.setMainApp(await this._defaultAppBuilder.loadBody({
					reg: REG.reg,
					appDef: appDef || {},
					hasHeadTitleBar: true
				}))) throw "";
			} else {
				if (await this.closeMainApp() === false) throw "";
			}
		} catch (e) {
			//Echec Ã  la mise Ã  jour de l'app => on restaure le prÃ©cÃ©dent hash
			await ERROR.log("Failed to load app", e);
			this.updateUrlHash();
		}
	}

	/**
	 * MÃ©thode publique commune Ã  Tout AppFrameXxxDeskFeat.
	 * Permet d'adapter l'ouverture de l'app en fonction de l'AppFrame.
	 *
	 * @param options Options d'affichage
	 */
	async findAndOpenApp(appDef: JAppDef, ev?: Event, options?: any): Promise<boolean> {
		for (const bd of this._appBuilders) {
			if (bd.isAppDefMatch(appDef)) {
				const nmAppDef: JAppDef = {};
				Object.assign(nmAppDef, appDef);
				if ("normalizeAppDef" in bd) bd.normalizeAppDef(nmAppDef);
				if ((ev instanceof MouseEvent || ev instanceof KeyboardEvent) && ACTION.isAccelPressed(ev)) {
					const url = new URL(document.location.href);
					url.hash = CDM.stringify(nmAppDef);
					window.open(url.href, "_blank");
					return true;
				}
				await this.setMainApp(await bd.loadBody({reg: REG.reg, appDef: nmAppDef, hasHeadTitleBar: true}));
				return true;
			}
		}
		return false;
	}

	/**
	 * MÃ©thode publique commune Ã  Tout AppFrameXxxDeskFeat.
	 *
	 * @param options Options d'affichage
	 */
	async openApp(appBuilder: IAppBuilder<any, any>, appDef: JAppDef, options?: any) {
		await this.setMainApp(await appBuilder.loadBody({reg: REG.reg, appDef, hasHeadTitleBar: true}));
	}

	protected fAppIniting: Promise<boolean>;

	protected async setMainApp<ENV>(app: IApp<ENV>): Promise<boolean> {
		if (this.fAppIniting) this.fAppIniting = (this.fAppIniting as any).finally(() => this.fAppIniting = this.setMainAppChained(app));
		else this.fAppIniting = this.setMainAppChained(app);
		return this.fAppIniting;
	}

	protected async setMainAppChained<ENV>(app: IApp<ENV>): Promise<boolean> {
		if (this.mainCtn && (this._auth.currentUser || app.anonymousAllowed)) {
			const oldApp = this._app;
			if (oldApp && await (oldApp === app ? this.hideMainApp() : this.closeMainApp()) === false) return false;
			this._app = this.mainCtn.appendChild(app);
			VIEWS.onViewShown(app);
			await this._app.initializedAsync;
			this.refreshTitle();
			this.updateUrlHash();
			if (this._app !== app || this._app.appErrorStatus != null) return false;
		} else {
			this._app = app; //en attente...
		}
		return true;
	}

	async gotoHome(): Promise<boolean> {
		if (this._defaultAppBuilder) {
			return this.setMainApp(await this._defaultAppBuilder.loadBody({reg: REG.reg, appDef: {}, hasHeadTitleBar: true}));
		} else {
			const ok = await this.closeMainApp();
			if (ok) this.updateUrlHash();
			return ok;
		}
	}

	/**
	 * @param replace Pas d'historique. cf https://developer.mozilla.org/en-US/docs/Web/API/Location/replace
	 */
	protected updateUrlHash(replace?: boolean) {
		if (UrlHashObjDeskFeat.isIn(desk)) {
			try {
				this._updtHashFromHere = true;
				desk.setUrlHash(this._app ? this._app.appDef : null, replace);
			} finally {
				this._updtHashFromHere = false;
			}
		}
	}

	protected _updtHashFromHere: boolean;

	isMainAppAvailable() {return this._app != null}

	/** Masque l'app courante SANS la clore. */
	async hideMainApp(force?: boolean): Promise<boolean | undefined> {
		const app = this._app;
		const wasAlive = app && app.isConnected;
		if (wasAlive && !force && !(await VIEWS.canHideView(app))) return false;
		if (!await POPUP.closeAllPopup(app)) return false;
		if (this._app !== app) return undefined; //race cond, autre app affectÃ©e entre temps.
		this.mainCtn.textContent = null;
		if (wasAlive) VIEWS.onViewHidden(app);
		return true;
	}

	/** Retourne l'app courante. */
	public getMainApp(): IApp<any> | null {
		return this._app;
	}

	/**
	 * Clos l'app courante. Attention, url non mise Ã  jour.
	 * @return true si plus d'app courante. false si refus. undefined si race condition.
	 */
	protected async closeMainApp(force?: boolean): Promise<boolean | undefined> {
		const app = this._app;
		if (app) {
			try {
				if (!force && app.isConnected && !(await VIEWS.canHideView(app))) return false;
				if (!await POPUP.closeAllPopup(app)) return false;
				if (this._app !== app) return undefined; //race cond, autre app affectÃ©e entre temps.
				this._app = null;
				this.mainCtn.textContent = null;
				VIEWS.onViewHidden(app, true);
				this.refreshTitle();
			} catch (e) {
				//pour ne pas bloquer l'ouverture d'une autre app.
				console.log(e);
			}
		} else {
			this.mainCtn.textContent = null;
		}
		return true;
	}

	/**
	 * Recharge l'app courante (changement de contexte, de pref user...)
	 */
	async reloadMainApp(force?: boolean): Promise<boolean> {
		const appDef = this._app?.appDef;
		if (appDef && await this.closeMainApp(force)) return this.findAndOpenApp(appDef);
		return false;
	}

	showInfo(text: string) {
		// if (this.statusBar) {
		// 	this.statusBar.setStatus(text);
		// } else {
		POPUP.showNotif(text, document.body);
		//}
	}

	refreshTitle() {
		const appTitle = this._app ? VIEWS.getLabel(this._app) : null;
		document.title = appTitle ? appTitle + " - " + this.initialTitle : this.initialTitle;
		if (typeof this._app?.getHeadTitleBar === "function") {
			const bar = this._app.getHeadTitleBar();
			if (this.appBar.firstElementChild !== bar) {
				this.appBar.textContent = null;
				if (bar) this.appBar.appendChild(bar);
			}
		} else {
			this.appBar.textContent = null;
			this.appBar.append(<div class="appTitle">{appTitle}</div>);
		}
		if (Desk.electron) window.postMessage({type: "client:server:refreshTitle", appTitle, initialTitle: this.initialTitle}, location.origin);
	}

	static async requestFullscreen() {
		if (Desk.electron) {
			const exitButton = new Button().initialize({ uiContext: "dialog", label: "Quitter le mode plein-Ã©cran (Ãchap)" });
			const notif = POPUP.showNotifInfo(<p><span>L'application est dÃ©sormais en mode plein Ã©cran.</span>{exitButton}</p>, document.body, null, {viewPortY: "top"});
			exitButton.addEventListener('click', async () => {
				notif.close();
				await document.exitFullscreen();
			}, { once: true });
		}
		await document.body.requestFullscreen();
	}

	initialTitle: string;

	protected header: HTMLElement;
	protected mainBar: HTMLElement;
	protected appBar: HTMLElement;
	protected mainCtn: HTMLElement;

	protected _appBuilders: IAppBuilder<any, any>[];
	protected _defaultAppBuilder: IAppBuilder<any, any>;

	protected _auth: AuthSrv;

	/** app courante (affichÃ©e ou non) */
	protected _app: IApp<any> | null;
}

