/**
 * Feature du desk pour le choix entre le menu contextuel custom (applicatif) vs le menu du navigateur.
 * main : le menu applicatif est affichÃ© en 1er, le menu du navigateur en secondaire accessible via shift + click droit par ex.
 * secondary : le menu du navigateur est affichÃ© en 1er
 */
export class ContextMenuDeskFeat extends Desk {

	static add(desk: Desk, mode?: "main" | "secondary", reg: IReg<IBasicUniversePointer> = REG.reg): ContextMenuDeskFeat {
		if (this.isIn(desk)) return desk;
		const d = desk as ContextMenuDeskFeat;
		desk.assignProps(this);
		d.setContextMenuCustom(mode);

		document.body.addEventListener(Desk.electron ? "c-contextmenu" : "contextmenu", d.showContextMenu);
		REG.reg.addToList(Desk.LIST_accelKeys, " -accel", 1, new Action<any>('contextMenu').setExecute(function (c, ev) {
			d.showContextMenu.call(document.body, ev);
		}));

		REG.reg.addToList(Desk.LC_init, 'contextMenu', 1, () => {
			//Recherche du AuthServer
			const univ = reg.env.universe;
			const auth = univ ? univ.auth : null;
			if (auth) {
				if (auth.currentAuthenticatedUser) d.setContextMenuCustom(reg.getUserData("contextMenu"));
				auth.listeners.on("loggedUserChanged", () => {
					d.setContextMenuCustom(reg.getUserData("contextMenu"));
				});
			}
		});

		// DÃ©sactivation du comportement par dÃ©faut du shift+clic droit qui augmente la sÃ©lection textuelle jusqu'au curseur et empÃªche la correction orthographique
		document.body.addEventListener("mousedown", (ev) => {
			if (ev.button == 2 && ev.shiftKey) ev.preventDefault();
		});
		return d;
	}

	static isIn(desk: Desk): desk is ContextMenuDeskFeat {return 'contextMenuCustom' in desk}

	contextMenuCustom: "main" | "secondary" = "main";

	setContextMenuCustom(mode?: "main" | "secondary") {
		this.contextMenuCustom = mode !== "secondary" || Desk.electron ? "main" : mode;
	}

	/** AppelÃ© sur les events "contextmenu" (XXX Ã  revoir : et "keydown"). */
	showContextMenu(this: HTMLElement, ev: MouseEvent & CustomEvent) {
		if (ev instanceof MouseEvent) {
			if ((desk as ContextMenuDeskFeat).contextMenuCustom === "main") {
				//On laisse le menu contextuel natif sur shift + clickDroit
				if (ev.shiftKey) return;
			} else {
				//On affiche notre menu que sur shift + clickDroit
				if (!ev.shiftKey) return;
			}
			ev.preventDefault();
		}
		let node = DOMSH.findDeepActiveElement(DOMSH.findDocumentOrShadowRoot(this));
		while (node && node !== this) {
			if ('ctxMenuActions' in node) {
				const menu = (node as IContextMenuActionsPointer<any>).ctxMenuActions;
				if (menu && menu.actions && menu.actions.length > 0) {
					let anchor: OPopupAnchor;
					if ('pageX' in ev && ev.which > 0/* bloque si menu contextuel par clavier*/) {
						anchor = {
							initX: menu.rect ? GFX.bound(menu.rect.left, ev.pageX, menu.rect.right) : ev.pageX,
							initY: menu.rect ? GFX.bound(menu.rect.top, ev.pageY, menu.rect.bottom) : ev.pageY
						};
					} else if (menu.rect) {
						anchor = {
							initX: menu.rect.left,
							initY: menu.rect.bottom
						}
					} else {
						const focused = DOMSH.findDeepActiveElement() || ev.target as Element;
						anchor = {
							posFrom: focused, intersectWith: DOMSH.findFlatParentElt(focused, null, (n: Node): n is HTMLElement => {
								//FIXME pas satisfaisant
								return n instanceof HTMLElement ? n.scrollHeight > n.clientHeight : false;
							})
						};
					}
					//console.log("here::::::::::::::::::", anchor, menu.rect, ev);
					const from = DOMSH.findDeepActiveElement() as HTMLElement;

					if (Desk.electron) {
						// Enrichissement du actionContext par les informations de corrections ortho dans l'event c-contextmenu spÃ©cifique Ã  Electron
						if (ev.detail && ev.detail.misspelledWord) {
							menu.actionContext[ACTION.spellcheckSymbol] = {
								misspelledWord: ev.detail.misspelledWord,
								dictionarySuggestions: ev.detail.dictionarySuggestions,
							}
						} else {
							delete menu.actionContext[ACTION.spellcheckSymbol];
						}
					}

					return POPUP.showPopupActions({
						actions: menu.actions,
						actionContext: menu.actionContext,
						restoreFocus: from
					}, anchor, from || ev.target as Node);
				}
				return;
			}
			node = DOMSH.findParentElt(node);
		}
	}
}


/**
 * Ajoute la feature d'eval en amont du chargement de l'application si le navigateur implÃ©mente le standard Sec-Fetch-*.
 * Si le standard n'est pas implÃ©mentÃ©:
 * - affiche un message d'erreur Ã  l'utilisateur dans la page principale (comportement surchargeable),
 * - interrompt le processus d'init de la page.
 */
export class SecFetchDeskFeat extends Desk {

	/** Peut Ãªtre ajoutÃ© aprÃ¨s desk.launch(). */
	static add(desk: Desk): SecFetchDeskFeat {
		if (this.isIn(desk)) return desk;
		desk.assignProps(this);
		const d = desk as SecFetchDeskFeat;

		//En 1er : check de sÃ©curitÃ© si le navigateur implÃ©mente Sec-Fetch-*
		REG.reg.addToList(Desk.LC_init, 'checkSecFetch', 1, async () => {
			let secFetchAware: boolean;
			try {
				secFetchAware = await d.checkSecFetchSiteAware();
			} catch (e) {
				//AccÃ¨s au serveur impossible
				d.showErrorInfo(d.getNoNetworkMsg());
				//On interrompt le processus de tÃ©lÃ©chargement.
				throw e;
			}
			if (!secFetchAware) {
				d.showErrorInfo(d.getNoSecFetchMsg());
				//On interrompt le processus de tÃ©lÃ©chargement.
				throw Error("Sec-Fetch standard not available in this browser");
			}
		}, -100000);
		return d;
	}

	static isIn(desk: Desk): desk is SecFetchDeskFeat {return 'checkSecFetchSiteAware' in desk}

	async checkSecFetchSiteAware(): Promise<boolean> {
		return (REG.reg.env as IBasicUniversePointer).universe.isSecFetchSiteAware();
	}

	showErrorInfo(elt: HTMLElement) {
		document.body.textContent = null;
		document.body.append(elt);
	}

	getNoSecFetchMsg() {
		return <main>
			<h2>Votre navigateur est incompatible avec ce service</h2>
			<p>Votre navigateur n'assume pas une fonction nÃ©cessaire Ã  la sÃ©curitÃ© de ce service : <a href="https://w3c.github.io/webappsec-fetch-metadata">Fetch Metadata Request Headers</a></p>
			<p>Voici une liste de navigateurs connus qui dans leurs derniÃ¨res versions sont compatibles avec ce service :</p>
			<ul>
				{Desk.knownNavCompat.map((v) => v.url ? <li><a href={v.url}>{v.name}</a></li> : <li>{v.name}</li>)}
			</ul>
		</main>;
	}

	getNoNetworkMsg() {
		return <main>
			<h2>Serveur inaccessible</h2>
			<p>Le serveur ne semble pas accessible. VÃ©rifiez votre connexion et essayez ultÃ©rieurement.</p>
		</main>;
	}
}

